html {
  box-sizing: border-box;

}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  text-align: center;
  max-width: 1000px;
  margin: 0 auto;
  line-height: 1.3;
  font-family: 'Lato', sans-serif;
  background-image: url('./images/background.jpg');
  background-size:2560px 1440px;
}

#root {
  display:flex;
  flex-direction: column;
  margin: 0 9px;
}
